@import "/src/colors";

.background {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-color: $background-color;
}

.main-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $primary-color;
    width: 60vw;
    height: 36vw;
    border-radius: 5vw;
    padding: 1vw;
    color: white;
    z-index: 2;

    h2 {
        font-size: 1.7vw;
        font-weight: normal;
    }
}

.logo {
    object-fit: contain;
    height: 7vw;
}

.forms-container {
    display: flex;
    justify-content: space-between;
    background-color: $primary-color-dark;
    width: 100%;
    height: 100%;
    border-radius: 4vw;
    margin-top: 1vw;

    hr {
        background-color: $primary-color;
        height: 90%;
        width: 1.3vw;
        margin: auto 0;
        border: 0;
        border-radius: 2vw;
    }
}


.circle {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 1.8vw;
    top: 1.8vw;
    border: 0.4vw solid white;
    border-radius: 50%;
    width: 3vw;
    height: 3vw;
    cursor: pointer;
    transition: 0.3s;

    p {
        font-size: 2vw;
    }
}

.circle:hover {
    border-color: $background-color;
    color: $background-color;
}


.floating-img {
    object-fit: contain;
    position: absolute;
    width: 100vw;
    height: auto;
    pointer-events: none;
}

.top-floating-img {
    top: 0;
}

.bottom-floating-img {
    bottom: 0;
    transform: scaleY(-1) scaleX(-1);
}

.floating-circle {
    width: auto;
}

